import React from 'react';
import classes from './contactData.css';



class ContactData extends React.Component{
    state={
        name:'',
        email:'',
        address:{
            street:'',
            postalCode:''
        }
    }
    orderHandler=()=>{
        
    }
    render(){
        return (
            <div className={classes.ContactData}>
                <h1>Enter your contact data</h1>
                <form>
                    <label className={classes.Label}>Name:</label>
                    <input type="text" name="name" className={classes.input}/>
                    <label className={classes.Label}>Email:</label>
                    <input type="text" name="email" className={classes.input}/>
                    <label className={classes.Label}>street:</label>
                    <input type="text" name="street" className={classes.input}/>
                    <label className={classes.Label}>Postal Code:</label>
                    <input type="text" name="zipcode" className={classes.input}/>
                    <button onClick={this.orderHandler}>Order Now</button>
                </form>
            </div>
        );
    }
}


export default ContactData;


